@charset "UTF-8";
/*

Arduino® Light Theme - Stefania Mellai <s.mellai@arduino.cc>

*/
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #FFFFFF; }

.hljs,
.hljs-subst {
  color: #434f54; }

.hljs-keyword,
.hljs-attribute,
.hljs-selector-tag,
.hljs-doctag,
.hljs-name {
  color: #00979D; }

.hljs-built_in,
.hljs-literal,
.hljs-bullet,
.hljs-code,
.hljs-addition {
  color: #D35400; }

.hljs-regexp,
.hljs-symbol,
.hljs-variable,
.hljs-template-variable,
.hljs-link,
.hljs-selector-attr,
.hljs-selector-pseudo {
  color: #00979D; }

.hljs-type,
.hljs-string,
.hljs-selector-id,
.hljs-selector-class,
.hljs-quote,
.hljs-template-tag,
.hljs-deletion {
  color: #005C5F; }

.hljs-title,
.hljs-section {
  color: #880000;
  font-weight: bold; }

.hljs-comment {
  color: rgba(149, 165, 166, 0.8); }

.hljs-meta-keyword {
  color: #728E00; }

.hljs-meta {
  color: #728E00;
  color: #434f54; }

.hljs-emphasis {
  font-style: italic; }

.hljs-strong {
  font-weight: bold; }

.hljs-function {
  color: #728E00; }

.hljs-number {
  color: #8A7B52; }
